<template>
	<div>
		<div class="head-layout">
			<div class="head-nav">
				<div @click="cutRouter('0')" :class="color==='0'?'color-entry2':''" class="all-pointer color-entry">Booster ECO</div>
				<div @click="cutRouter('1')" :class="color==='1'?'color-entry2':''" class="all-pointer color-entry">Ecoswap</div>
				<div @click="cutRouter('2')" class="all-pointer color-entry">Justswap</div>
				<div @click="cutRouter('3')" :class="color==='3'?'color-entry2':''" class="all-pointer color-entry">About</div>
				<div @click="cutRouter('4')" :class="color==='4'?'color-entry2':''" class="all-pointer color-entry" v-if="isOwner">Owner</div>
			</div>
			<div style="display: flex; justify-content: flex-end">
				<el-button id="head-btn" size="small" type="primary" style="background: #34CECC; border: 1px #34CECC solid;" :loading="loadingBtn">Connect</el-button>
				<!-- <div id="head-btn" class="all-btn head-btn all-pointer" @click="connectWallet()">Connect</div> -->
				<div id="head-addr" class="head-addr" style="display: none;">
					<div style="display: flex;">
						<!-- <span id="head-balance" style="background: #F7F8FA; border-radius: 15px 15px; padding: 8px 30px 8px 15px">
							-
						</span> -->
						<span id="head-address" style="background: #EDEEF2; border-radius: 15px; padding: 8px 20px 8px 15px; position: relative; right: 25px;">
							-
						</span>
					</div>
				</div>
				<el-select size="mini" v-model="value" placeholder="Select" id="lang" @change="changeLang()">
				    <el-option
				      v-for="item in options"
				      :key="item.value"
				      :label="item.label"
				      :value="item.value">
					  <div style="display: flex; align-items: center ">
						  <span style="margin-right: 5px;">{{item.label}}</span>
						  <img :src="item.src" style="height: 15px; width: 20px;" />
					  </div>
				    </el-option>
				  </el-select>
			</div>
		</div>
		<router-view />
	</div>
</template>

<script>
	export default {
		data() {
			return {
				// 控制导航栏的颜色
				color: '0',
				// connect按钮加载
				loadingBtn: false,
				// 语言选择框
				options: [
				    {
              value: 'EN',
              label: 'EN',
						  src: './static/img/EN.jpg',
            },
            {
              value: 'MYS',
              label: 'MYS',
              src: './static/img/MYS.jpg',
            },
            {
              value: 'ZH',
              label: 'ZH',
						  src: './static/img/ZH.jpg',
				    },
            {
              value: 'HINDI',
              label: 'HINDI',
              src: './static/img/HINDI.png',
            },
            {
              value: 'JP',
              label: 'JP',
              src: './static/img/JP.jpg',
            }],
        // value: 'EN',
        value: localStorage.lang,
        // 管理员钱包地址, 根据钱包地址显示管理员页面
				isOwner: false,

			}
		},

		async created() {
			this.cutRouter2(this.$route.path);
			this.loadingBtn = true;
			this.connectWallet();
		},

		methods: {
			// 刷新或从进页面根据路由选择导航栏颜色
			cutRouter2(r) {
				if(r === '/') {
					this.color = '0';
				}else if(r === '/tickets') {
					this.color = '1';
				}else if(r === '/about') {
					this.color = '3';
				}else if(r === '/owner') {
					this.color = '4';
				}
			},

			// 跳转路由
			cutRouter(index) {
				let u = '';
				if(this.$route.query.referrer) {
					u = '?referrer=' + this.$route.query.referrer;
				}
				if (index === '0') {
					this.$router.replace('/'+u);
					this.color = '0';
				} else if (index === '1') {
					this.$router.replace('/tickets'+u);
					this.color = '1';
				} else if (index === '2') {
					window.open("https://justswap.network/?lang=en-US#/home");
				} else if (index === '3') {
					this.$router.replace('/about'+u);
					this.color = '3';
				}  else {
					this.$router.replace('/owner'+u);
					this.color = '4';
				}
			},

			// 连接钱包
			async connectWallet() {
				console.log('点击了连接钱包');
				// 判断有木有波场钱包环境
				// 获取钱包对象
				let res = await this.$contract.isTronLink();
				// 给出无法点击弹框, 逼着用户必须退出去准备波场环境, 否者无法继续使用
				if(!res.status) {
					console.log('没有发现钱包');
					return;
				}
				// 有钱包
				console.log('有钱包');
				// 创建合约对象
				await this.$contract.createdObject();
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				document.getElementById('head-addr').style.display = 'block';
				document.getElementById('head-btn').style.display = 'none';
				document.getElementById('head-address').innerText = this.$util.addressCurtail(address);

				// 显示管理员页面
				// 判断是不是两个合约的管理者
				let res5 = await this.$contract.masterManager(address);
				let res6 = await this.$contract.ticketsManager(address);
				// 判断是不是两个合约的owner;
				let res7 = await this.$contract.getMasterOwner(address);
				let res8 = await this.$contract.getTicketsOwner(address);
				res5 = res5.isManager;
				res6 = res6.isManager;
			    res7 = await this.$contract.addressFromHex(res7);
				res8 = await this.$contract.addressFromHex(res8);
				console.log('是不是管理员地址', res5, res6, res7, res8, address);
				if(res7 === address || res8 === address || res5 || res6) {
					this.isOwner = true;
				}
				// 同步锁, 执行完后才会执行其他页面的$onLaunched函数; 执行到这说明波场钱包对象和波场合约对象都已经获取到了;
				this.loadingBtn = false;
				await this.$isResolve();
			},

			// 改变语言
			changeLang() {
        localStorage.lang=this.value;
				this.$i18n.locale = this.value;
			},


		}
	}
</script>

<style>
	/* 默认样式 */
	.el-select .el-input__inner:focus {
	    border-color: #DCDFE6;
	}

	.color-entry {
		color: #666666;
		font-weight: bold;
	}

	.color-entry2 {
		color: #34CECC;
		font-weight: bold;
	}

	#head-btn {
		padding: 7px 8px;
		border-radius: 5px;
	}

	/* 宽度尺度大于1500时 */
	@media only screen and (min-width: 415px) {
		.head-layout {
			display: flex;
			justify-content: space-between;
			padding: 30px 30px 20px 20px;
		}

		.head-nav {
			display: flex;
		}

		.head-nav>div {
			font-size: 1.15rem;
			margin-right: 35px;
		}

		#lang {
			margin-left: 10px;
			width: 80px;
			height: 35px;
		}
		/* .el-input--mini .el-input__inner {
		    height: 35px;
		    line-height: 28px;
		} */
		#head-address {
			font-size: 15px;
		}
	}

	/* 宽度尺寸小于414时 */
	@media only screen and (max-width: 414px) {
		.head-layout {
			display: flex;
			flex-direction: column;
		}

		.head-nav {
			display: flex;
			padding: 20px 5px 10px;
		}

		.head-nav>div {
			text-align: center;
			flex-grow: 1;
			font-size: 0.9rem;
			font-weight: bold;
		}

		.head-btn {
			text-align: right;
			align-self: flex-end;
			margin-right: 10px;
		}

		.head-addr {
			align-self: flex-end;
			margin-bottom: 5px;
		}
		#lang {
			margin-left: 10px;
			width: 70px;
			height: 30px;
		}
		/* .el-input--mini .el-input__inner {
		    height: 35px;
		    line-height: 28px;
		} */
		#head-address {
			font-size: 12px;
		}
	}
</style>



